<template>
  <div class="container">
    <div class="row">
      <div class="col col-md-3 col-lg-3 col-xl-3 col-sm-auto">
        <nav id="featuresnav" class="navbar navbar-expand-sm navbar-toggleable-sm navbar-light bg-white border-bottom box-shadow mb-3">
          <div class="flex-column">
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsenav" aria-controls="navbarSupportedContent"
                    aria-expanded="false" aria-label="Toggle navigation">
              <span class="navbar-toggler-icon"></span>
            </button>
            <div id="collapsenav" class="navbar-collapse collapse d-sm-inline-flex">
              <div class="nav flex-column nav-pills" role="tablist" aria-orientation="vertical">
                <a class="nav-link active" id="decompilation-tab" data-toggle="pill" href="#">Decompilation</a>
              </div>
            </div>
          </div>
        </nav>
      </div>
      <div class="col-md col-lg col-xl">
        <div class="tab-content">
          <div class="tab-pane fade show active" role="tabpanel" aria-labelledby="decompilation-tab">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Features',
    beforeRouteEnter (to, from, next) {
      //  confirm 
      //  `this`
      // 
      next(vm => {
        var linkto = document.getElementById(vm.$route.name.toLowerCase() + '-tab')
        linkto.className = 'nav-link active'
      })
    }
  }
</script>

<style scoped>
</style>



