<template>
  <div class="about">
    <div class="container">
      <p class="lead">FernflowerUI is licensed under <a href="http://www.apache.org/licenses/LICENSE-2.0">Apache License 2.0</a></p>
      <p class="lead">
        Great thanks to the following projects:
        <ul class="list-group list-group-flush col-4">
          <li class="list-group-item">
            <a class="text-monospace" href="https://github.com/fesh0r/fernflower">Fernflower</a>
          </li>
          <li class="list-group-item">
            <a class="text-monospace" href="https://docs.microsoft.com/en-us/cpp/mfc/mfc-desktop-applications?view=vs-2019">Microsoft Foundation Classes</a>
          </li>
          <li class="list-group-item">
            <a class="text-monospace" href="https://vuejs.org/">Vue.js</a>
          </li>
          <li class="list-group-item">
            <a class="text-monospace" href="https://getbootstrap.com/">Bootstrap</a>
          </li>
          <li class="list-group-item">
            <a class="text-monospace" href="https://nodejs.org/">Node.js</a>
          </li>
        </ul>
      </p>
    </div>
  </div>
</template>

<script>
  export default {
  name: 'About',
  beforeRouteEnter (to, from, next) {
    //  confirm 
    //  `this`
    // 
    next(vm => {
      var linkto = document.getElementById(vm.$route.name.toLowerCase() + '-tab')
      linkto.className = 'nav-link active'
    })
  }
}
</script>

<style scoped>
</style>



