import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Download from '@/components/Download'
import About from '@/components/About'
import Features from '@/components/Features'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: '',
      component: Home
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home
    },
    {
      path: '/Features',
      name: 'Features',
      component: Features
    },
    {
      path: '/Download',
      name: 'Download',
      component: Download
    },
    {
      path: '/About',
      name: 'About',
      component: About
    }
  ]
})



