// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false

router.afterEach((to, from) => {
  if (to.name != null) {
    var nameto = to.name === '' ? 'Home' : to.name
    var linkto = document.getElementById(nameto.toLowerCase() + '-tab')
    if (linkto != null) {
      linkto.className = 'nav-link active'
    }
    document.title = nameto + ' - FernflowerUI'
  }

  if (from.name != null) {
    var namefrom = from.name === '' ? 'Home' : from.name
    var linkfrom = document.getElementById(namefrom.toLowerCase() + '-tab')
    if (linkfrom != null) {
      linkfrom.className = 'nav-link'
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



